<template>
  <div class="container">
    <div class="head-row" :class="{active: allActorList.length < 5}"  ref="headList">
      <div class="avatar-row" v-for="(item, index) in allActorList" :key="index">
        <img :src="item.memberHead"/>
        <div class="tag" v-if="item.role === 0"><span>拼主</span></div>
      </div>
    </div>
    <div class="num-text" >
      <span v-if="[1,2].indexOf(groupInfo.viewStatus) > -1">还差{{groupInfo.clustersNum - groupInfo.actors.length}}人即可拼团成功，<count-down :time="groupInfo.endTime - groupInfo.sysTime" @finish="onTimeDown"/>后结束</span>
      <span v-if="groupInfo.viewStatus === 3">恭喜您拼团成功了</span>
      <span v-if="groupInfo.viewStatus === 4 && groupInfo.groupBuyToActorVos && groupInfo.groupBuyToActorVos.length">此团已满，推荐你加入以下团</span>
    </div>
    <div class="other-group" v-if="groupInfo.viewStatus === 4 && groupInfo.groupBuyToActorVos && groupInfo.groupBuyToActorVos.length">
      <img :src="groupInfo.groupBuyToActorVos[0].actorList[0].memberHead"/>
      <div class="name">{{groupInfo.groupBuyToActorVos[0].actorList[0].memberName}}</div>
      <div class="rest-row">
        <span>还差{{groupInfo.groupBuyToActorVos[0].leftNum}}人拼成</span>
        <span style="margin-top: .08rem">还剩<count-down :time="groupInfo.groupBuyToActorVos[0].endTime - groupInfo.groupBuyToActorVos[0].sysTime" @finish="onTimeDown"/></span>
      </div>
      <div class="join-button" @click="doJoin(groupInfo.groupBuyToActorVos[0].actorList[0].groupId)">
        <span>去参团</span>
      </div>
    </div>
    <div class="button" @click="doBtnClick(groupInfo.viewStatus, groupInfo)">
      <span v-if="groupInfo.viewStatus === 1">邀人来拼团</span>
      <span v-if="groupInfo.viewStatus === 2 ">立即参与</span>
      <span v-if="groupInfo.viewStatus === 3">继续逛逛</span>
      <span v-if="groupInfo.viewStatus === 4">我要开团</span>
    </div>
    <!-- 商品规格 -->
    <van-popup v-model="showInfoFlag" style=" border-radius: 12px 12px 0px 0px;"  position="bottom" get-container="#app-warp">
      <lazy-attr-modal
        v-if="productInfo.sgAttribute && productInfo.sgStoreAttribute"
        :sgAttribute="productInfo.sgAttribute"
        :sgStockInfo="dynamicInfo.sgStockInfo"
        :sgStoreAttribute="productInfo.sgStoreAttribute"
        :checkedAttr="productInfo.checkedAttr"
        :stepperNum="stepperNum"
        :ioucInfo="ioucInfo"
        :btnType="10"
        :hasSubProduct='hasSubProduct'
        :activityInfo="dynamicInfo.activityInfo"
        :attrLength="attrLen"
        :priceInfo="dynamicInfo.priceInfo"
        :notSaleStatus="notSaleStatus"
        :baseInfo="dynamicInfo.baseInfo"
        :products="productInfo.products"
        @attrConfirm="attrConfirm"
        @fnShowInfo="fnShowInfo"
        @fnChangeProductNumber="fnChangeProductNumber"
        @changeGoodsNumber="changeGoodsNumber"
        @attrNameChange="attrNameChange"
        @fnSelectAttribute="fnSelectAttribute"
        @buy="buy"
        @fullBuy="buy"
      />
    </van-popup>
    <!-- 虚拟 spu -->
    <!-- <van-popup v-model="showSpuFlag" style=" border-radius: 12px 12px 0px 0px;"  position="bottom" get-container="#app-warp">
      <lazy-spu-modal
        v-if="spuInfo"
        :productId="productId"
        :spuInfo="spuInfo"
        :sgAttribute="sgAttribute"
        :sgStockInfo="sgStockInfo"
        :sgStoreAttribute="sgStoreAttribute"
        :checkedAttr="checkedAttr"
        :stepperNum="stepperNum"
        :checkCouponIsAllGet="checkCouponIsAllGet"
        :getGoodsUseCouponInfo="getGoodsUseCouponInfo"
        :ioucInfo="ioucInfo"
        :btnType="btnType"
        :hasSubProduct='hasSubProduct'
        :activityInfo="activityInfo"
        :attrLength="attrLength"
        :baseInfo="baseInfo"
        :priceInfo="priceInfo"
        :swiperImgs="swiperImgs"
        :productNumber="productNumber"
        :notSaleStatus="notSaleStatus"
        :products="products"
        @attrConfirm="spuConfirm"
        @fnShowInfo="fnShowInfo"
        @fnChangeProductNumber="fnChangeProductNumber"
        @attrNameChange="attrNameChange"
        @fnSelectSpu="fnSelectSpu"
        @addCart="fnAddCart"
        @buy="buy"
        @depositBuy="depositBuy"
        @fullBuy="fullBuy"
        @priceValue="changePriceValue"
        @fnArrivalNotice="fnArrivalNotice"
      />
    </van-popup> -->
  </div>
</template>
<script>
import { CountDown } from 'vant';
import UplusApi from '@uplus/uplus-api';
import semver from '@/utils/semver'
import { mapGetters } from 'vuex'
import {
  dynamic,
  getIoucCost,
  getProductBasicInfo,
} from '@/http/service/goods'
import ClipboardJS from 'clipboard';
import lazyAttrModal from '@/views/goods/detail/components/attr-modal' // 复制插件
const instance = new UplusApi()
instance.initDeviceReady()
// const lazyAttrModal = import('@/views/goods/detail/components/attr-modal')
// const lazySpuModal = import('@/views/goods/detail/components/spu-modal.vue')
export default {
  components: {
    CountDown,
    lazyAttrModal,
    // lazySpuModal,
  },
  props: {
    groupInfo: {
      type: Object,
      default: () => {}
    },
  },
  data() {
    return {
      timeDown: 5000000,
      showInfoFlag: false,
      // showSpuFlag: false,
      viewStatus: 0, // 1-参团人-邀人来拼团 2-非参团人立即参与 3-参团人继续逛逛 4-非参团人我要开团
      attrHasSelect: false, // 标记规格是否是选中状态 单规格默认选中，多规格默认不选中
      productInfo: { // 单品静态信息
        products: {},
        sgAttribute: {},
        sgStoreAttribute: [],
        storeId: '',
        checkedAttr: ''
      },
      dynamicInfo: { // 单品动态信息
        activityInfo: {},
        baseInfo: {},
        sgStockInfo: {},
        storeInfo: {},
        priceInfo: {},
      }, // 动态信息
      hasSubProduct: false, // 是否含有子商品，多规格逻辑需要针对处理
      stepperNum: 1, // 选择数量
      notSaleStatus: false, // 是否不可购买状态 false可购买 true 不可购买
      ioucInfo: { feeInfo: '查看更多' }, // 白条信息
      sku: '',
      skku: '', //
      attrLen: 0, // 选中规格长度
      attrValueNames: '', // 选中规格名称
      groupId: 0, // 参团Id
    }
  },
  watch: {
    groupInfo(newVal) {
      if (newVal.viewStatus === 2 || newVal.viewStatus === 4) { // 可参团时获取商品信息
        this.getProductInfo();
      }
    },
  },
  mounted() {
  },
  computed: {
    ...mapGetters({
      location: 'getlocationInfo',
    }),
    ...mapGetters([
      'mId',
      'accessToken',
      'ucId'
    ]),
    allActorList() {
      const list = [];
      const actorsLength = this.groupInfo.actors.length;
      for (let index = 0; index < this.groupInfo.clustersNum; index++) {
        if (index < actorsLength) {
          list.push(this.groupInfo.actors[index])
        } else {
          list.push({
            memberHead: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/groupBooking/icon-head-default.png',
            role: 1,
          })
        }
      }
      return list;
    },
  },
  methods: {
    goGoodsDetail(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${prod.storeId}`)
    },
    doBtnClick(status, groupInfo) {
      switch (status) {
        case 1:
          this.doShare(groupInfo.actors[0].groupId)
          break;
        case 2:
          this.doJoin();
          break;
        case 3:
          this.goHomePage();
          break;
        case 4:
          this.goGoodsDetail(groupInfo);
          break;
      };
    },
    /**
     * 邀人参团
     */
    doShare() {
      const shareUrl = location.href;
      if (this.$util.isHaierApp()) {
        this.$util
          .inAppShare({
            title: '海尔智家',
            desc: '海尔智家好物拼团，邀你参与',
            url: shareUrl,
          })
      } else {
        const clipboard = new ClipboardJS('.button', {
          text: function(trigger) {
            return shareUrl
          }
        });

        clipboard.on('success', () => {
          this.$toast.success('链接复制成功');
        });

        clipboard.on('error', () => {
          this.$toast.success('复制失败');
        });
      }
    },
    /** 返回首页 */
    goHomePage() {
      if (this.$util.isHaierApp()) {
        instance.initDeviceReady().then(() => {
          instance.upAppinfoModule.getAppInfo().then(result => {
            let appVersion = result.retData.appVersion
            console.log(appVersion, 'appVersion')
            if (appVersion && semver.gte(appVersion, '6.20.0')) {
              console.log('aaaa')
              this.$util.openNewWindow('https://uplus.haier.com/uplusapp/customize/index.html?close_current_page=true', true, true)
              //
            } else {
              console.log('bbb')
              instance.initDeviceReady().then(function() {
                instance.closeH5ContainerView();
              });
            }
          })
        })
      } else {
        this.$router.push({ path: '/' })
      }
    },
    /** 参团 */
    doJoin(groupId) {
      this.groupId = groupId;
      this.fnShowInfo();
    },
    /** 获取单品静态信息 */
    getProductInfo() {
      getProductBasicInfo({
        productId: this.groupInfo.productId,
        channel: '',
        openId: '',
        storeId: this.getShareId || '', // 分享人id
      }).then(res => {
        if (res.success) {
          this.productInfo = res.data;
          this.hasSubProduct = (!!res.data.sgStoreAttribute[0].haschildItems)
          if (this.hasSubProduct) {
            if (this.skku) {
              // 多规格 参与限时抢购商品 进入单品页默认选中 活动规格 价格最低的规格组合
              res.data.sgStoreAttribute.forEach(i => {
                if (i.skku === this.skku) {
                  this.productInfo.checkedAttr = i.attrValueIds
                }
              })
            } else {
              // this.shopInfo.data.checkedAttr = ''
              // 如果是有优惠券的且多规格商品默认选中 this.shopInfo.data.checkedAttr 的商品
              let defaultItem = this.productInfo.sgStoreAttribute.filter(item => item.attrValueIds === this.productInfo.checkedAttr)[0];
              console.log(defaultItem, 'defaultItem')
              if (defaultItem) {
                this.sku = defaultItem.sku
                this.skku = defaultItem.skku;
                // this.dynamic();
              }
            }
          } else {
            // 单规格商品默认规格选中，不可取消
            this.attrHasSelect = true
            // 非多规格 默认选中
            this.productInfo.checkedAttr = '0'
          }
          this.dynamic();
        }
      });
    },
    /** 获取单品动态信息 */
    dynamic() {
      return new Promise((resolve, reject) => {
        dynamic({
          prodId: this.groupInfo.productId,
          // memberStoreId: this.getShareId || '',
          number: this.stepperNum || 1, // 不能传0
          // number: 1, // 不能传0
          provinceId: this.location.provinceId,
          cityId: this.location.cityId,
          regionId: this.location.districtId,
          streetId: this.location.townshipId,
          latitude: this.location.latitude,
          longitude: this.location.longitude,
          skku: this.skku || null,
          sku: this.productInfo.products.sku,
          visitStore: this.groupInfo.storeId,
          // exclusivePriceId: _q.exclusiveId || null
        }).then(res => {
          if (res.success) {
            this.dynamicInfo = res.data;
            this.fetchIoucCost()
          }
        })
      })
    },
    /**
     * @description 获取白条分期的信息
     */
    fetchIoucCost() {
      if (this.$util.isNull(this.accessToken) || this.$util.isNull(this.ucId)) {
        // this.$toast('您的当前帐号暂时无法访问此服务,请使用关联手机号登录')
        return
      }
      let price = 0
      if (this.dynamicInfo.activityInfo) {
        price = this.dynamicInfo.priceInfo.finalPrice
      } else {
        price = this.dynamicInfo.priceInfo.actualPrice
      }
      getIoucCost({
        payAmt: price,
        proGroup: this.productInfo.products.department,
        sku: this.skku ? this.skku : this.groupInfo.sku,
        token: this.accessToken,
      }).then(res => {
        if (res.success) {
          this.ioucInfo = res.data // { 'crdComAvailAmt': '100', 'costInfo': [{ 'rate': 0.65, 'fee': 3.24, 'num': 3, 'eachPrinAndFee': 169.57 }, { 'rate': 0.65, 'fee': 3.24, 'num': 6, 'eachPrinAndFee': 86.41 }, { 'rate': 0.65, 'fee': 3.24, 'num': 12, 'eachPrinAndFee': 44.82 }], 'feeInfo': '', 'applyStatus': '3' }//
        } else {
          // this.$toast(res.message)
        }
      })
    },
    /**
     * @description 处理静态接口中的多规格数据
     */
    calcAttr() {
      let _attrValueNames = []
      // 多规格处理
      for (let key in this.dynamicInfo.sgAttribute) {
        _attrValueNames.push(this.dynamicInfo.sgAttribute[key][0].attrName)
      }
      this.holdSelected = _attrValueNames.join(',');

      if (this.hasSubProduct) {
        // 多规格情况 因为默认不选中， 选中规格长度手动置空
        this.attrLen = 0 // 初始化的时候算一下长度
      }
      // this.originAttrs = this.dynamicInfo.sgStoreAttribute ? this.dynamicInfo.sgStoreAttribute.filter((v, i) => v.num >= 0) : []
    },
    onTimeDown() {
      this.$emit('refreshData');
    },
    buy() {
      // 立即购买
      this.$emit('fnGoBuy', 1)
    },
    /**
     * @description 多规格确定回调
     */
    attrConfirm() {
      if (!this.$store.getters.getLoginStatus) {
        // this.islogining = true
        this.$store.dispatch('doLogin')
        // .then(res => {
        //   if (res !== 400) {
        //     this.islogining = false
        //     this.reload()
        //   } else {
        //     this.islogining = false
        //   }
        // }).catch(() => {
        //   this.islogining = false
        // })
      }
      this.showInfoFlag = false;
      let _proList = [
        {
          name: this.hasSubProduct ? this.attrValueNames || null : null, // 只有含子商品多规格才传规格名称
          sku: this.hasSubProduct ? this.skku || null : null, // 只有含子商品多规格才传skku
          proId: this.groupInfo.productId,
          num: this.stepperNum || 1,
          visitStore: this.groupInfo.storeId,
          exclusivePriceId: ''
        },
      ]
      let queryParam = {
        _proList: _proList,
        ac: 0,
        isExclusive: false,
        originSource: this.originSource,
        groupId: this.groupInfo.actors[0].groupId, // 拼团Id ac为0 groupId为0代表是团长
        // coupon: coupon
      }
      console.log('createOrder-Param', queryParam);
      this.$router.push({
        name: 'create',
        query: queryParam
      })
    },
    fnShowInfo (value) {
      // this.btnType = value // 0:加入购物车 1:立即购买
      // 如果spu信息存在 走虚拟spu弹窗展示逻辑 否则走多规格逻辑
      // if (this.spuInfo) {
      //   this.showSpuFlag = !this.showSpuFlag
      //   this.$emit('titleSelectOption', !this.showSpuFlag)
      // } else {
      this.showInfoFlag = !this.showInfoFlag
      // }
    },
    attrNameChange(v) {
      this.attrValueNames = v;
      this.attrLen = (this.attrValueNames.length === 0 ? '' : this.attrValueNames.split(',').length);
      this.attrValueNames = (v.length === 0 ? this.holdSelected : this.attrValueNames);
    },
    fnChangeProductNumber(value) {
      this.stepperNum = value;
      this.$emit('fnChangeProductNumber', value)
    },
    changeGoodsNumber(value) {
      this.stepperNum = value;
      this.$emit('changeGoodsNumber', value)
    },
    fnSelectAttribute (item, stepperNum) {
      // // 多规格 规格未选完 不刷新
      // if (!data.price) return
      // this.stepperNum = this.stepperNum ? this.stepperNum : (this.newStockNum > 0 ? 1 : 0);
      // this.$emit('fnSelectAttribute', data, this.stepperNum)

      // this.sgStoreAttributeItem = item
      this.skku = item.skku
      this.stepperNum = stepperNum
      // 重新请求商品信息，更新数据
      this.dynamic()
    },
  }
}
</script>
