<script>
/**
 * 拼团详情
 */
import skeleton from '@/components/skeleton'
import bookingNumRow from './component/bookingNumRow'
import {
  getMainGroupBooking,
  getGroupBuyByOrderSn
} from '@/http/service/groupBooking'
import { mapGetters } from 'vuex'
const guessYouLick = () => import('@/components/guessYouLick')
export default {
  components: {
    skeleton,
    bookingNumRow,
    guessYouLick,
  },
  data() {
    return {
      isSkeletonLoading: true,
      orderSn: '55555',
      groupInfo: {
        actors: [],
      },
      moreList: [],
      freeze: Object.freeze({
        localData: {
          orderSn: '',
        }
      }),
    }
  },
  created() {
    this.isSkeletonLoading = false;
  },
  mounted() {
    this.freeze.localData.orderSn = this.$route.query.orderSn || ''
    this.getGroupDetail()
    this.getMoreList()
  },

  computed: {
    ...mapGetters({
      location: 'getlocationInfo',
    })
  },
  methods: {
    goShop(prod) {
      this.$router.push(`/shopDetail?storeId=${prod.storeId}`)
    },
    goGoodsDetail(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${prod.storeId}`)
    },
    /** 获取拼团详情 */
    getGroupDetail() {
      getGroupBuyByOrderSn({ orderSn: this.freeze.localData.orderSn }).then(res => {
        this.isSkeletonLoading = false;
        if (res.success) {
          this.groupInfo = res.data;
        }
      });
    },
    /** 获取更多拼团 */
    getMoreList() {
      getMainGroupBooking({ streetId: this.location.townshipId }).then(res => {
        if (res.success) {
          res.data.map(item => {
            item.sellPoint = item.sellPoints != null ? item.sellPoints.split(',') : []
          })
          this.moreList = res.data || [];
        } else {
          this.$toast(res.message);
        }
      });
    },
    /**
     * 标签排序
     */
    sortList(arr) {
      let len = arr.length;
      for (var i = 0; i < len; i++) {
        for (var j = 0; j < len - 1 - i; j++) {
          if (arr[j].tagOrder > arr[j + 1].tagOrder) {
            var temp = arr[j + 1]
            arr[j + 1] = arr[j]
            arr[j] = temp
          }
        }
      }
      return arr
    }
  }
}
</script>
